	module.exports = {"data":{"markdownRemark":{"html":"<h2>Graph Databases</h2>\n<p>A graph database is a database that uses graph structures for semantic queries with nodes, edges and properties to represent and store data. A key concept of the system is the graph (or edge or relationship), which directly relates data items in the store. The relationships allow data in the store to be linked together directly, and in many cases retrieved with one operation.</p>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<ul>\n<li><a href='https://en.wikipedia.org/wiki/Graph_database' target='_blank' rel='nofollow'>Graph Databases</a></li>\n</ul>","fields":{"slug":"/computer-science/databases/graph-databases/"},"frontmatter":{"title":"Graph Databases"}}},"pathContext":{"slug":"/computer-science/databases/graph-databases/"}}

